body {
  font-size: 16px;
}

a:hover {
  text-decoration: none;
  color: #007bff;
}

.dropdown-menu a:visited {
  color: #212529;
  display: block;
  background-color: #9c508c32;
}

.dropdown-menu .btn.focus,
.btn:focus {
  box-shadow: 0 0 0 0rem rgb(0 0 0);
}

.bg-main {
  background-color: #802c6e;
}

.nav-item span {
  padding: 10px;
}

.badge {
  top: -25px;
  right: -5px;
}

.cart-link {
  color: white;
  text-decoration: none;
}

.cart-link:hover {
  color: white;
  text-decoration: none;
}

.products img {
  height: 300px;
  cursor: pointer;
}

table img {
  height: 50px;
}

table .btn {
  padding: 0.1875rem 0.75rem;
}

table .title {
  width: 100%;
  max-width: 40%;
}

.product_image:hover {
  transform: scale(1.3, 1.3);
  -webkit-transform: scale(1.3, 1.3);
  -moz-transform: scale(1.3, 1.3);
  -ms-transform: scale(1.3, 1.3);
  -o-transform: scale(1.3, 1.3);
  transition: all 0.1s;
  -webkit-transition: all 0.1s;
  -moz-transition: all 0.1s;
  -ms-transition: all 0.1s;
  -o-transition: all 0.1s;
}

.form-control:focus {
  border-color: #ced4da;
  box-shadow: 0 0 0 0.2rem #802c6e25;
}

.form-control {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.form-inline button {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.navbar-nav .dropdown-menu {
  position: absolute;
}

#topIcon {
  position: fixed;
  opacity: .7;
  right: 50px;
  bottom: 50px;
  font-size: 30px;
  color: #802c6e;
  cursor: pointer;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
#topIcon:hover{
  opacity: 1;
}

html {
  scroll-behavior: smooth;
}
